'use client';

import { useState } from 'react';

export type Role =
  | 'Staff'
  | 'Customer'
  | 'Station Manager'
  | 'Company Owner'
  | 'Private Owner'
  | 'Agent'
  | 'Technician'
  | 'Investor'
  | 'Admin';

const DEFAULT_COMPANY_ID = 'engkXQIeqS7QNJPWKPrw'; // Xe ƒêi·ªán Trung Nam

export function useRentBikeForm(role: Role) {
  const [loading, setLoading] = useState(false);

  const [companyId, setCompanyId] = useState(DEFAULT_COMPANY_ID);
  const [stationId, setStationId] = useState('');
  const [customerName, setCustomerName] = useState('');
  const [phone, setPhone] = useState('');
  const [idNumber, setIdNumber] = useState('');
  const [address, setAddress] = useState('');

  const [vehicleModel, setVehicleModel] = useState('');
  const [vehicleColor, setVehicleColor] = useState('');
  const [vin, setVin] = useState('');
  const [licensePlate, setLicensePlate] = useState('');

  const [batteryCodes, setBatteryCodes] = useState<string[]>([]);
  const [boxType, setBoxType] = useState<string[]>([]);

  const [subscriptionPackageId, setSubscriptionPackageId] = useState('');
  const [basePrice, setBasePrice] = useState('');
  const [pinFee, setPinFee] = useState('');
  const [accessories, setAccessories] = useState('');
  const [total, setTotal] = useState('');
  const [deposit, setDeposit] = useState('');
  const [remaining, setRemaining] = useState('');
  const [note, setNote] = useState('');

  const handleSubmit = async () => {
    setLoading(true);
    try {
      const bookingData = {
        companyId,
        stationId,
        customerName,
        phone,
        idNumber,
        address,
        vehicleModel,
        vehicleColor,
        vin,
        licensePlate,
        batteryCodes,
        boxType,
        subscriptionPackageId,
        basePrice: Number(basePrice),
        pinFee: Number(pinFee),
        accessories,
        total: Number(total),
        deposit: Number(deposit),
        remaining: Number(remaining),
        note,
        role,
      };

      console.log('üì§ Submitting booking form:', bookingData);
      return true;
    } catch (error) {
      console.error('‚ùå Booking submission failed:', error);
      return false;
    } finally {
      setLoading(false);
    }
  };

  return {
    companyId, setCompanyId,
    stationId, setStationId,
    customerName, setCustomerName,
    phone, setPhone,
    idNumber, setIdNumber,
    address, setAddress,

    vehicleModel, setVehicleModel,
    vehicleColor, setVehicleColor,
    vin, setVin,
    licensePlate, setLicensePlate,

    batteryCodes, setBatteryCodes,
    boxType, setBoxType,

    subscriptionPackageId, setSubscriptionPackageId,
    basePrice, setBasePrice,
    pinFee, setPinFee,
    accessories, setAccessories,
    total, setTotal,
    deposit, setDeposit,
    remaining, setRemaining,
    note, setNote,

    loading,
    handleSubmit,
  };
}
