"use client";

import { useEffect, useRef, useState } from 'react';
import { Input } from '@/src/components/ui/input';
import { Button } from '@/src/components/ui/button';
import { db } from '@/src/firebaseConfig';
import { collection, getDocs } from 'firebase/firestore';
import { Label } from '@/src/components/ui/label';
import QrScanner from 'qr-scanner';

interface QRCodeScannerProps {
  onScanned: (bikeInfo: any) => void;
}

export default function QRCodeScanner({ onScanned }: QRCodeScannerProps) {
  const [vehicleID, setVehicleID] = useState('');
  const [matchingBikes, setMatchingBikes] = useState<any[]>([]);
  const [mode, setMode] = useState<'scan' | 'search'>('search');
  const videoRef = useRef<HTMLVideoElement | null>(null);
  const scannerRef = useRef<QrScanner | null>(null);

  useEffect(() => {
    if (mode === 'scan' && videoRef.current) {
      const scanner = new QrScanner(
        videoRef.current,
        (result) => {
          if (result?.data) {
            setVehicleID(result.data);
            handleScanned(result.data);
          }
        },
        {
          returnDetailedScanResult: true,
        }
      );
      scanner.start();
      scannerRef.current = scanner;
      return () => scanner.stop();
    }
  }, [mode]);

  const fetchMatchingBikes = async (query: string) => {
    const snapshot = await getDocs(collection(db, 'ebikes'));
    const bikes = snapshot.docs.map(doc => ({ id: doc.id, ...(doc.data() as any) }));

    // Only search if there is input, otherwise don't show suggestions
    const filtered = query.trim()
      ? bikes.filter(bike =>
          bike.vehicleID?.toLowerCase().includes(query.toLowerCase())
        )
      : [];

    setMatchingBikes(filtered);
  };

  useEffect(() => {
    if (mode === 'search') {
      fetchMatchingBikes(vehicleID);
    }
  }, [vehicleID, mode]);

  const handleScanned = async (id: string) => {
    const snapshot = await getDocs(collection(db, 'ebikes'));
    const bike = snapshot.docs
      .map(doc => ({ id: doc.id, ...(doc.data() as any) }))
      .find(b => b.vehicleID?.toLowerCase() === id.toLowerCase());
    if (bike) onScanned(bike);
  };

  const handleSelectBike = (bike: any) => {
    onScanned(bike);
    setVehicleID(bike.vehicleID);
    setMatchingBikes([]);
  };

  return (
    <div className="space-y-4">
      <div className="flex gap-2">
        <Button onClick={() => setMode('search')} variant={mode === 'search' ? 'default' : 'outline'}>
          Search Vehicle ID
        </Button>
        <Button onClick={() => setMode('scan')} variant={mode === 'scan' ? 'default' : 'outline'}>
          Scan QR Code
        </Button>
      </div>
      {mode === 'search' && (
        <div className="space-y-2">
          <Label>Enter part of Vehicle ID</Label>
          <div className="relative">
            <Input
              placeholder="e.g. DE0182"
              value={vehicleID}
              onChange={(e) => setVehicleID(e.target.value)}
              className="pr-10"
            />
            {matchingBikes.length > 0 && (
              <ul className="absolute z-10 w-full bg-white border rounded shadow-md mt-1 max-h-40 overflow-y-auto">
                {matchingBikes.map(bike => (
                  <li
                    key={bike.id}
                    className="cursor-pointer px-4 py-2 hover:bg-gray-100"
                    onClick={() => handleSelectBike(bike)}
                  >
                    {bike.vehicleID} - {bike.plateNumber || 'No Plate'}
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>
      )}
      {mode === 'scan' && (
        <div className="space-y-2">
          <Label>Scan QR Code with Camera</Label>
          <video ref={videoRef} className="w-full max-w-md border rounded" playsInline muted autoPlay />
        </div>
      )}
    </div>
  );
}
