'use client';

import { useEffect, useState } from 'react';
import Header from '@/src/components/landingpage/Header';
import Footer from '@/src/components/landingpage/Footer';
import RentBikeFormFactory from '@/src/components/rent/factory/RentBikeFormFactory';
import { auth, db } from '@/src/firebaseConfig';
import { doc, getDoc } from 'firebase/firestore';
import { onAuthStateChanged } from 'firebase/auth';

export default function RentPage() {
  const [role, setRole] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (user) {
        try {
          const userDoc = await getDoc(doc(db, 'users', user.uid));
          const data = userDoc.exists() ? userDoc.data() : {};
          const fetchedRole = data?.role || 'Customer'; // ‚úÖ l·∫•y ƒë√∫ng role t·ª´ Firestore
          console.log('‚úÖ Role from Firestore:', fetchedRole);
          setRole(fetchedRole);
        } catch (err) {
          console.error('‚ùå Error fetching user role:', err);
          setRole('Customer');
        }
      } else {
        setRole('Customer'); // fallback n·∫øu kh√¥ng ƒëƒÉng nh·∫≠p
      }
      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  if (loading) return <div className="text-center py-10">Loading...</div>;

  return (
    <div className="min-h-screen flex flex-col bg-gray-100">
      <Header />

      <main className="flex-1 py-10">
        <div className="text-center text-gray-600 text-sm mb-4">
          <strong>üîê Current Role:</strong> {role}
        </div>
        {role && <RentBikeFormFactory role={role} />}
      </main>
      <Footer />
    </div>
  );
}
