// xedientrungnam/RentBikeFormFactory.tsx
'use client';

import { useEffect, useState } from 'react';
import RentEbikeMobileForm from '../xedientrungnam/RentEbikeMobileForm';
import RentEbikeDesktopForm from '../xedientrungnam/RentEbikeDesktopForm';
import { Role } from '../xedientrungnam/useRentBikeForm';
import { convertRoleToDisplayRole } from '@/src/utils/roleUtils';

interface Props {
  role: string; // ƒë·∫ßu v√†o t·ª´ Firestore ho·∫∑c Auth
}

export default function RentBikeFormFactory({ role }: Props) {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkScreenSize = () => {
      setIsMobile(window.innerWidth < 768);
    };

    checkScreenSize();
    window.addEventListener('resize', checkScreenSize);
    return () => window.removeEventListener('resize', checkScreenSize);
  }, []);

  const displayRole: Role = convertRoleToDisplayRole(role);
  
  return isMobile ? (
    <RentEbikeMobileForm role={displayRole} />
  ) : (
    <RentEbikeDesktopForm role={displayRole} />
  );
}
