'use client';

import { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';

interface PackageSelectorProps {
  companyId: string;
  selectedId: string;
  onSelect: (pkg: SubscriptionPackage) => void;
}

export default function PackageSelector({
  companyId,
  selectedId,
  onSelect,
}: PackageSelectorProps) {
  const [packages, setPackages] = useState<SubscriptionPackage[]>([]);
  const [loading, setLoading] = useState(false);

  const fetchPackages = async () => {
    setLoading(true);
    try {
      const q = query(collection(db, 'subscriptionPackages'), where('companyId', '==', companyId));
      const snapshot = await getDocs(q);
      const data = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
      })) as SubscriptionPackage[];
      setPackages(data);
    } catch (error) {
      console.error('‚ùå Error loading packages:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (companyId) {
      fetchPackages();
    }
  }, [companyId]);

  return (
    <select
      className="w-full border rounded px-3 py-2"
      value={selectedId}
      onChange={(e) => {
        const selected = packages.find(p => p.id === e.target.value);
        if (selected) onSelect(selected);
      }}
      disabled={loading}
    >
      <option value="">
        {loading ? 'üîÑ Loading packages...' : '-- Select a package --'}
      </option>
      {packages.map((pkg) => (
        <option key={pkg.id} value={pkg.id} title={pkg.note || ''}>
          {pkg.name}
        </option>
      ))}
    </select>
  );
}
